import React, { useState,useContext } from 'react';
import { MyContext } from './data';
import './app.css';
import * as firebase from 'firebase';
import loading from './25.gif';
import ReactDOM from 'react-dom';


function App() {

  const data = useContext(MyContext)

  const auth=firebase.auth()
  const db=firebase.firestore()

  auth.onAuthStateChanged(
    d=>console.log(d)
    
  )

  const check=async()=>{
      const email=document.getElementById('input_user').value
      const password=document.getElementById('input_password').value
      await auth.signInWithEmailAndPassword(email,password)
      .then(res=>{
        window.location.href='/home'
      }
      )
      .catch(err=>{
        document.getElementById('alert').innerText='Email or Password is incorrect!'
        ReactDOM.render(
          <div>
          </div>,document.getElementById('loading')
        )  
      })
  }


  const sub=(e)=>{
    e.preventDefault();
    ReactDOM.render(
      <div>
        <img src={loading} alt='loading'/>
      </div>,document.getElementById('loading')
    )
    if(document.getElementById('input_password').value==='' && document.getElementById('input_user').value===''){
      document.getElementById('alert').innerText='Email and Password is Empty'
      ReactDOM.render(
        <div>
        </div>,document.getElementById('loading')
      )
      return
    }
    if(document.getElementById('input_password').value===''){
      document.getElementById('alert').innerText='Password Is Empty'
      ReactDOM.render(
        <div>
        </div>,document.getElementById('loading')
      )
      return
    }
    if(document.getElementById('input_user').value===''){
      document.getElementById('alert').innerText='User Is Empty'
      ReactDOM.render(
        <div>
        </div>,document.getElementById('loading')
      )
      return
    }
    else{
      check()
    }
    
  }

  const anime=(e)=>{
    if(e==='input_user'){
      let user=document.getElementById('user')
      user.style.bottom='69%'
      user.style.color='#74b9ff'
    }
    else{
      let password=document.getElementById('password')
      password.style.bottom='52%'  
      password.style.color='#74b9ff'
    }
  }

  const reset=()=>{
    if(document.getElementById('input_user').value===''){
      let user=document.getElementById('user')
      user.style.bottom='59%'
      user.style.color='#000000'
    }
    if(document.getElementById('input_password').value===''){
      let password=document.getElementById('password')
      password.style.bottom='42%'
      password.style.color='#000000'
    }
    if(document.getElementById('input_user').value.length>0){
      let user=document.getElementById('user')
      user.style.bottom='69%'
      user.style.color='#636e72'
    }
    if(document.getElementById('input_password').value.length>0){
      let password=document.getElementById('password')
      password.style.bottom='52%'
      password.style.color='#636e72'
    }
  }

  return (
    <div className="App">
    <form id='login'>
      <span id='title'>Login</span>
      <div id='user'>Email</div>
      <input id='input_user' onClick={(e)=>anime(e.target.id)} onBlur={reset}/>
      <div id='password'>Password</div>
      <input id='input_password' type='password' onClick={(e)=>anime(e)} onBlur={reset}/>
      <button onClick={(e)=>sub(e)}>Login</button>
      <div id='info'><a href='/register'>New Account?Register</a></div>
    </form>
    <div id='alert'></div>
    <div id='loading'></div>  
    </div>
  );
}

export default App;
