import React from 'react';
import './reg.css';
import * as firebase from 'firebase';
import loading from './25.gif';
import ReactDOM from 'react-dom';

const Register=()=>{

    const auth=firebase.auth()

    const sub=async(e)=>{
        e.preventDefault()
        ReactDOM.render(
          <div>
            <img src={loading} alt='loading'/>
          </div>,document.getElementById('loading')
        )
        const email=document.getElementById('input_user').value
        const password=document.getElementById('input_password').value
        await auth.createUserWithEmailAndPassword(email,password)
        .then(res=>{
            console.log(res)
            auth.signOut()
            window.location.href='/login'
        })
        .catch(
          err=>{
            document.getElementById('alert').innerText='Error check for Password must be of 6 character or Email is taken!'
            ReactDOM.render(
              <div>
              </div>,document.getElementById('loading')
            )
          }
        )

    }

    const anime=(e)=>{
        if(e==='input_user'){
          let user=document.getElementById('reg-user')
          user.style.bottom='69%'
          user.style.color='#74b9ff'
        }
        else{
          let password=document.getElementById('reg-password')
          password.style.bottom='52%'  
          password.style.color='#74b9ff'
        }
      }
      const reset=()=>{
        if(document.getElementById('input_user').value===''){
          let user=document.getElementById('reg-user')
          user.style.bottom='59%'
          user.style.color='#000000'
        }
        if(document.getElementById('input_password').value===''){
          let password=document.getElementById('reg-password')
          password.style.bottom='42%'
          password.style.color='#000000'
        }
        if(document.getElementById('input_user').value.length>0){
          let user=document.getElementById('reg-user')
          user.style.bottom='69%'
          user.style.color='#636e72'
        }
        if(document.getElementById('input_password').value.length>0){
          let password=document.getElementById('reg-password')
          password.style.bottom='52%'
          password.style.color='#636e72'
        }
      }
    
    return(
        <div id='reg-cover'>
            <div id='register'>
                <form id='reg-contain'>
                    <div id='title'>Register</div>
                    <div id='reg-user' type='email'>Email</div>
                    <input id='input_user' onClick={(e)=>anime(e.target.id)} onBlur={reset}/>
                    <div id='reg-password'>Password</div>
                    <input id='input_password' type='password' onClick={(e)=>anime(e)} onBlur={reset}/>
                    <button onClick={(e)=>sub(e)}>Register</button>
                </form>
                <div id='info'><a href='/login'>Have a account? Login</a></div>  
            </div>  
              <div id='alert'></div>
              <div id='loading'></div>  

        </div>
    )
}
export default Register;